.button {
  min-width: 200px;
  min-height: 42px;
  color: #fff;
  border: none;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  padding: 0;
  font-size: 16px;
  font-weight: 700;
  line-height: 1.19;
  display: flex;
  position: relative;
  overflow: hidden;
}

.button:after, .button:before {
  content: "";
  display: block;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.button:before {
  width: 100%;
  height: 100%;
  background-image: linear-gradient(90.03deg, #00c5eb -1.97%, #b544fe 89.43%);
  background-repeat: no-repeat;
  background-size: cover;
}

.button:after {
  width: calc(100% - 2px);
  height: calc(100% - 2px);
  background-color: #06071b;
  border-radius: 5px;
  transition: all .3s;
  display: none;
}

.button__text {
  z-index: 10;
  position: relative;
}

.button:hover:after, .button:focus:after {
  display: block;
}

